import { connect } from 'react-redux';
import { change } from 'redux-form';
import { withRouter } from 'react-router';

import actions from '../../actions';
import SpecialitySelector from '../../components/panels/SpecialitySelector';

const mapStateToProps = state => ({
  isLoading: state.entities.specialities.isLoading,
  city: state.form.search.values.city.Id,
  items: state.entities.specialities.list.map(speciality => ({
    key: speciality.Id,
    text: speciality.Name,
    speciality
  }))
});

const mapDispatchToProps = (dispatch, { history }) => ({
  load: city =>
    dispatch({
      type: actions.LOAD_SPECIALITIES,
      payload: { city }
    }),
  onSelect: item => {
    dispatch(change('search', 'speciality', item.speciality));
    history.goBack();
  }
});

export default withRouter(
  connect(
    mapStateToProps,
    mapDispatchToProps
  )(SpecialitySelector)
);
