import { connect } from 'react-redux';
import { withRouter } from 'react-router';
import { change } from 'redux-form';

import actions from '../../actions';
import { getSearchParams } from '../../selectors';
import StationSelector from '../../components/panels/StationSelector';

const mapStateToProps = state => ({
  isLoading: state.entities.stations.isLoading,
  city: getSearchParams(state).city.Id,
  items: state.entities.stations.list.map(station => ({
    station,
    text: station.Name,
    key: station.Id
  }))
});

const mapDispatchToProps = (dispatch, { history }) => ({
  load: city => dispatch({ type: actions.LOAD_STATIONS, payload: { city } }),
  onSelect: item => {
    dispatch(change('search', 'station', item ? item.station : null));
    history.goBack();
  }
});

export default withRouter(
  connect(
    mapStateToProps,
    mapDispatchToProps
  )(StationSelector)
);
