import React from 'react';
import PropTypes from 'prop-types';
import { withRouter } from 'react-router';
import { Root, View } from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';

import match from './utils/match';
import Search from './components/views/Search';
import Order from './containers/panels/Order';

const routes = [
  { id: 'order', params: { path: '/order' } },
  { id: 'search', params: { path: '/' } }
];

class App extends React.Component {
  static propTypes = {
    location: PropTypes.shape({
      pathname: PropTypes.string
    }).isRequired
  };

  render() {
    const {
      location: { pathname }
    } = this.props;
    const matched = match(routes, pathname);
    return (
      <Root activeView={matched ? matched.id : 'search'}>
        <Search id="search" />
        <View id="order" activePanel="orderPanel">
          <Order id="orderPanel" />
        </View>
      </Root>
    );
  }
}

export default withRouter(App);
