import React from 'react';
import PropTypes from 'prop-types';
import { FormLayout, FormLayoutGroup } from '@vkontakte/vkui';
import { reduxForm, propTypes, Field } from 'redux-form';
import connect from '@vkontakte/vkui-connect';

import TextField from '../atoms/TextField';
import PhoneField from '../atoms/PhoneField';

const validate = values => {
  const errors = {};
  if (!values.firstName) {
    errors.firstName = 'Укажите имя';
  }
  if (!values.lastName) {
    errors.lastName = 'Укажите фамилию';
  }
  if (!/79(\d){9}/.test(values.phone)) {
    errors.phone = 'Укажите корректный телефонный номер';
  }
  return errors;
};

class OrderForm extends React.Component {
  static propTypes = {
    ...propTypes,
    disabled: PropTypes.bool
  };

  static defaultProps = {
    disabled: false
  };

  componentDidMount() {
    connect.send('VKWebAppGetUserInfo', {});
    connect.send('VKWebAppGetPhoneNumber', {});
  }

  render() {
    const { handleSubmit, disabled } = this.props;
    return (
      <FormLayout onSubmit={handleSubmit}>
        <FormLayoutGroup top="Имя">
          <Field name="firstName" component={TextField} disabled={disabled} />
        </FormLayoutGroup>
        <FormLayoutGroup top="Фамилия">
          <Field name="lastName" component={TextField} disabled={disabled} />
        </FormLayoutGroup>
        <FormLayoutGroup top="Номер телефона">
          <Field
            name="phone"
            component={PhoneField}
            type="tel"
            disabled={disabled}
            parse={value => value.replace(/[\\+\s-\\(\\)]/g, '')}
          />
        </FormLayoutGroup>
      </FormLayout>
    );
  }
}

export default reduxForm({
  form: 'order',
  validate
})(OrderForm);
