import React from 'react';
import PropTypes from 'prop-types';
import moment from 'moment';

import Location from '../moleculs/Clinic/Location';
import Order from '../moleculs/Clinic/Order';
import Info from '../moleculs/Clinic/Info';

import styles from './Clinic.module.css';

const Clinic = ({
  date,
  clinic: {
    ClinicId,
    ClinicName,
    Price,
    SpecialPrice,
    Street,
    House,
    StationsDistance,
    Slots
  },
  onOrder
}) => {
  const now = moment();
  const slots = (Slots || []).filter(s => {
    const startTime = moment(s.StartTime);
    return startTime.isAfter(now) && startTime.isSame(moment(date), 'day');
  });
  return (
    <div className={styles.Clinic}>
      <Info name={ClinicName} price={Price} specialPrice={SpecialPrice} />
      <Location
        house={House}
        street={Street}
        stationsDistance={StationsDistance}
      />
      <Order
        slots={slots}
        onOrderClick={() => onOrder({ date, clinicId: ClinicId })}
        onSlotSelected={slot => onOrder({ slot, clinicId: ClinicId })}
      />
    </div>
  );
};

Clinic.defaultProps = {
  date: undefined,
  onOrder: () => {}
};

Clinic.propTypes = {
  date: PropTypes.string,
  clinic: PropTypes.shape({
    ClinicId: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
    ClinicName: PropTypes.string,
    Price: PropTypes.number,
    SpecialPrice: PropTypes.number,
    Street: PropTypes.string,
    House: PropTypes.string,
    Slots: PropTypes.array,
    StationsDistance: PropTypes.array
  }).isRequired,
  onOrder: PropTypes.func
};

export default Clinic;
