import React from 'react';
import PropTypes from 'prop-types';
import { View } from '@vkontakte/vkui';
import { withRouter } from 'react-router';

import match from '../../utils/match';
import Home from '../../containers/panels/Home';
import SearchResults from '../../containers/panels/SearchResults';
import CitySelector from '../../containers/panels/CitySelector';
import SpecialitySelector from '../../containers/panels/SpecialitySelector';
import StationSelector from '../../containers/panels/StationSelector';
import Doctor from '../../containers/panels/Doctor';

const routes = [
  { id: 'home', params: { path: '/', exact: true } },
  { id: 'search', params: { path: '/search' } },
  { id: 'cities', params: { path: '/cities' } },
  { id: 'stations', params: { path: '/stations' } },
  { id: 'specialities', params: { path: '/specialities' } },
  { id: 'doctor', params: { path: '/doctor/:id' } }
];

// TODO: Выпилить костыль
class Search extends View {
  static matched(nextProps) {
    const {
      location: { pathname }
    } = nextProps;
    return match(routes, pathname);
  }

  shouldComponentUpdate(nextProps) {
    return typeof Search.matched(nextProps) !== 'undefined';
  }

  render() {
    const { id } = this.props;
    const { id: activePanel, params } = Search.matched(this.props);
    return (
      <View activePanel={activePanel} id={id}>
        <Home id="home" />
        <SearchResults id="search" />
        <CitySelector id="cities" />
        <SpecialitySelector id="specialities" />
        <StationSelector id="stations" />
        <Doctor id="doctor" params={params} />
      </View>
    );
  }
}

Search.propTypes = {
  location: PropTypes.shape({
    pathname: PropTypes.string
  }).isRequired
};

export default withRouter(Search);
