export default experience => {
  if (experience >= 11 && experience <= 14) {
    return `${experience} врачей`;
  }
  if (experience % 10 === 1) {
    return `${experience} врач`;
  }
  if ([2, 3, 4].includes(experience % 10)) {
    return `${experience} врача`;
  }
  return `${experience} врачей`;
};
