import { connect } from 'react-redux';
import { withRouter } from 'react-router';

import Home from '../../components/panels/Home';
import { getSearchParams } from '../../selectors';
import actions from '../../actions';

const mapStateToProps = (state, { history }) => {
  const { city } = getSearchParams(state);
  return {
    isInitialized: state.app.isInitialized,
    withSubway: [2, 3].includes(city.SearchType),
    selectCity: () => history.push('/cities'),
    selectSpeciality: () => history.push('/specialities'),
    selectStation: () => history.push('/stations')
  };
};

const mapDispatchToProps = (dispatch, { history }) => ({
  onSearch: query => {
    dispatch({
      type: actions.SEARCH,
      payload: {
        city: query.city.Id,
        speciality: query.speciality.Id,
        when: query.date,
        subwayStation: query.station && query.station.Id,
        page: 0
      }
    });
    history.push('/search');
  }
});

export default withRouter(
  connect(
    mapStateToProps,
    mapDispatchToProps
  )(Home)
);
