import React from 'react';
import PropTypes from 'prop-types';
import { Button } from '@vkontakte/vkui';

import SlotButtons from '../../atoms/Slots';

import styles from './Order.module.css';

const Order = ({ slots, onSlotSelected, onOrderClick }) => (
  <div className={styles.Order}>
    {slots.length > 0 ? (
      <SlotButtons slots={slots} onSelected={onSlotSelected} />
    ) : (
      <div>
        <div className={styles.OrderButton}>
          <Button
            level="commerce"
            stretched
            onClick={e => {
              e.stopPropagation();
              onOrderClick();
            }}
          >
            Записаться
          </Button>
        </div>
        <div className={styles.OrderDescription}>
          В этой клинике у врача нет онлайн-расписания.
          <br />
          Оставьте заявку и мы перезвоним в течении 10 минут.
        </div>
      </div>
    )}
  </div>
);

Order.defaultProps = {
  slots: [],
  onOrderClick: () => {},
  onSlotSelected: () => {}
};

Order.propTypes = {
  slots: PropTypes.arrayOf(PropTypes.object),
  onOrderClick: PropTypes.func,
  onSlotSelected: PropTypes.func
};

export default Order;
