import React from 'react';
import PropTypes from 'prop-types';
import { withRouter } from 'react-router';
import { Panel, PanelHeader, Spinner } from '@vkontakte/vkui';

import BackButton from '../atoms/BackButton';
import FilterableList from '../organisms/FilterableList';
import './SpecialitySelector.css'; // TODO: Заменить на модуль

class SpecialitySelector extends React.Component {
  componentWillMount() {
    const { city, load } = this.props;
    load(city);
  }

  render() {
    const { id, isLoading, onSelect, items, history } = this.props;
    return (
      <Panel id={id} className="SpecialitySelector">
        <PanelHeader left={<BackButton onClick={() => history.goBack()} />}>
          Выбор специальности
        </PanelHeader>
        {isLoading ? (
          <Spinner size="large" />
        ) : (
          <FilterableList items={items} onSelect={onSelect} />
        )}
      </Panel>
    );
  }
}

SpecialitySelector.propTypes = {
  id: PropTypes.string.isRequired,
  city: PropTypes.string.isRequired,
  isLoading: PropTypes.bool.isRequired,
  load: PropTypes.func.isRequired,
  history: PropTypes.object.isRequired,
  onSelect: PropTypes.func.isRequired
};

export default withRouter(SpecialitySelector);
