import React from 'react';
import PropTypes from 'prop-types';
import { Search, List, Cell } from '@vkontakte/vkui';

import FilterableList from './FilterableList';
import StationItem from '../moleculs/StationItem';

class StationList extends FilterableList {
  render() {
    const { filtered } = this.state;
    const { onSelect } = this.props;
    return (
      <div>
        <Search theme="default" onChange={this.onChange} />
        <List>
          <Cell onClick={() => onSelect(null)}>Во всем городе</Cell>
          {filtered.map(item => (
            <Cell key={item.key} onClick={() => onSelect(item)}>
              <StationItem station={item.station} />
            </Cell>
          ))}
        </List>
      </div>
    );
  }
}

StationList.propTypes = {
  ...FilterableList.propTypes,
  items: PropTypes.arrayOf(
    PropTypes.shape({
      key: PropTypes.string,
      text: PropTypes.string,
      station: PropTypes.object
    })
  ).isRequired
};

export default StationList;
