import React from 'react';
import PropTypes from 'prop-types';
import { withRouter } from 'react-router';
import { Panel, PanelHeader, Spinner } from '@vkontakte/vkui';

import BackButton from '../atoms/BackButton';
import StationList from '../organisms/StationList';
import styles from './StationSelector.module.css';

class StationSelector extends React.Component {
  componentWillMount() {
    const { city, load } = this.props;
    load(city);
  }

  render() {
    const { id, isLoading, items, history, onSelect } = this.props;
    return (
      <Panel id={id}>
        <PanelHeader left={<BackButton onClick={() => history.goBack()} />}>
          Метро
        </PanelHeader>
        {isLoading ? (
          <div className={styles.Spinner}>
            <Spinner size="large" />
          </div>
        ) : (
          <StationList items={items} onSelect={onSelect} />
        )}
      </Panel>
    );
  }
}

StationSelector.propTypes = {
  id: PropTypes.string.isRequired,
  isLoading: PropTypes.bool.isRequired,
  city: PropTypes.string.isRequired,
  load: PropTypes.func.isRequired,
  items: PropTypes.arrayOf(PropTypes.object).isRequired,
  history: PropTypes.object.isRequired,
  onSelect: PropTypes.func.isRequired
};

export default withRouter(StationSelector);
