import actions from '../actions';

export default (state = { isLoading: false, doctor: null }, action) => {
  switch (action.type) {
    case actions.SELECT_DOCTOR:
      return {
        ...state,
        doctor: action.payload.doctor
      };
    case actions.LOAD_DOCTOR:
      return {
        ...state,
        isLoading: true
      };
    case actions.LOAD_DOCTOR_SUCCESS:
      return {
        ...state,
        isLoading: false,
        doctor: action.payload.doctor
      };
    default:
      return state;
  }
};
