import { connect } from 'react-redux';
import { change, clearFields } from 'redux-form';
import { withRouter } from 'react-router';

import CitySelector from '../../components/panels/CitySelector';

const mapStateToProps = state => ({
  items: state.entities.cities.map(city => ({
    key: city.Id,
    text: city.Name,
    city
  }))
});

const mapDispatchToProps = (dispatch, { history }) => ({
  onSelect: item => {
    dispatch(change('search', 'city', item.city));
    dispatch(clearFields('search', false, false, ['station']));
    history.goBack();
  }
});

export default withRouter(
  connect(
    mapStateToProps,
    mapDispatchToProps
  )(CitySelector)
);
