import React from 'react';
import PropTypes from 'prop-types';
import { Panel, PanelHeader, Tabs, TabsItem, Spinner } from '@vkontakte/vkui';
import moment from 'moment';

import BackButton from '../atoms/BackButton';
import Header from '../moleculs/DoctorDetail/Header';
import Clinic from '../organisms/Clinic';
import styles from './Doctor.module.css';
import Review from '../moleculs/Review';
import About from '../organisms/About';
import DateSelect from '../moleculs/DateSelect';

class Doctor extends React.Component {
  constructor(props) {
    super(props);
    const { date } = this.props;
    this.state = {
      selectedTab: 'order',
      selectedDate: date
    };
  }

  componentDidMount() {
    const {
      load,
      params: { id }
    } = this.props;
    load(id);
  }

  onDateChange(e) {
    this.setState({
      selectedDate: e.target.value
    });
  }

  selectTab(id) {
    this.setState({
      selectedTab: id
    });
  }

  render() {
    const {
      id,
      doctor,
      achievements,
      reviews,
      description,
      isLoading,
      clinicsLoading,
      title,
      back,
      onOrder
    } = this.props;
    const { selectedTab, selectedDate } = this.state;
    return (
      <Panel id={id}>
        <PanelHeader left={<BackButton onClick={back} />}>{title}</PanelHeader>
        <Header doctor={doctor} />
        {isLoading && (
          <div className={styles.Spinner}>
            <Spinner />
          </div>
        )}
        {!isLoading && (
          <div>
            <div className={styles.Tabs}>
              <Tabs style={{ background: 'white' }}>
                <TabsItem
                  onClick={() => this.selectTab('order')}
                  selected={selectedTab === 'order'}
                >
                  Записаться
                </TabsItem>
                <TabsItem
                  onClick={() => this.selectTab('reviews')}
                  selected={selectedTab === 'reviews'}
                >
                  Отзывы
                </TabsItem>
                <TabsItem
                  onClick={() => this.selectTab('about')}
                  selected={selectedTab === 'about'}
                >
                  О враче
                </TabsItem>
              </Tabs>
            </div>
            {selectedTab === 'order' && (
              <div>
                <div className={styles.DateSelect}>
                  <DateSelect
                    value={selectedDate}
                    onChange={e => this.onDateChange(e)}
                  />
                </div>
                {clinicsLoading && (
                  <div className={styles.Spinner}>
                    <Spinner />
                  </div>
                )}
                {!clinicsLoading &&
                  doctor.Clinics.map(c => (
                    <div key={c.Id} className={styles.Clinic}>
                      <Clinic
                        clinic={c}
                        date={selectedDate}
                        onOrder={params => onOrder({ ...params, doctor })}
                      />
                    </div>
                  ))}
              </div>
            )}
            {selectedTab === 'reviews' &&
              reviews.map(r => (
                <div key={r.Id} className={styles.Review}>
                  <Review review={r} />
                </div>
              ))}
            {selectedTab === 'about' && (
              <About description={description} achievements={achievements} />
            )}
          </div>
        )}
      </Panel>
    );
  }
}

Doctor.defaultProps = {
  description: '',
  reviews: [],
  achievements: {},
  date: moment().format('YYYY-MM-DD'),
  isLoading: true,
  clinicsLoading: false,
  title: null,
  params: {
    id: ''
  },
  load: () => {},
  onOrder: () => {}
};

Doctor.propTypes = {
  id: PropTypes.string.isRequired,
  load: PropTypes.func,
  onOrder: PropTypes.func,
  doctor: PropTypes.shape({
    Clinics: PropTypes.arrayOf(PropTypes.object)
  }).isRequired,
  achievements: PropTypes.shape(),
  reviews: PropTypes.arrayOf(PropTypes.object),
  description: PropTypes.string,
  date: PropTypes.string,
  isLoading: PropTypes.bool,
  clinicsLoading: PropTypes.bool,
  title: PropTypes.string,
  params: PropTypes.shape({
    id: PropTypes.string
  }),
  back: PropTypes.func.isRequired
};

export default Doctor;
