import actions from '../actions';

export default (state = { isLoading: false, list: [] }, { type, payload }) => {
  switch (type) {
    case actions.LOAD_STATIONS:
      if (payload.city === state.city && state.list.length > 0) {
        return { ...state, isLoading: false };
      }
      return {
        isLoading: true,
        list: [],
        city: payload.city
      };
    case actions.LOAD_STATIONS_SUCCESS:
      return {
        ...state,
        isLoading: false,
        list: payload.result
      };
    case actions.LOAD_STATIONS_FAIL:
      return {
        ...state,
        isLoading: false
      };
    default:
      return state;
  }
};
